






import Vue from 'vue';
import Component from 'vue-class-component';
import { State, Action } from 'vuex-class';

@Component
export default class ErrorSnackbar extends Vue {
  @State('error') error!: boolean;

  @Action('closeError') closeError!: () => void;

  updated() {
    console.log('clg');
    setTimeout(() => {
      this.closeError();
    }, 5000);
  }
}
