




















import Vue from 'vue';
import Component from 'vue-class-component';
import { Workout } from '@/types';
import { Action, State } from 'vuex-class';
import { getTime, parseJSON } from 'date-fns';
import Trash from '@/assets/Trash.svg';
import db from '@/db';
import WorkoutCard from './WorkoutCard.vue';
import Footer from './Footer.vue';

@Component({
  components: { WorkoutCard, Footer },
})
export default class Home extends Vue {
  @Action('init') init!: () => void;

  @State('workouts') workouts!: Workout[];

  trash = Trash;

  get workoutsSorted() {
    if (!this.workouts) {
      return [];
    }
    return this.workouts.sort((a, b) => {
      const timeA = getTime(parseJSON(a.creationDate || '')) || 0;
      const timeB = getTime(parseJSON(b.creationDate || '')) || 0;
      if (timeA > timeB) {
        return 1;
      }
      if (timeA < timeB) {
        return -1;
      }
      return 0;
    });
  }

  deleteAll() {
    db.destroy();
    window.location.reload();
  }

  mounted() {
    this.init();
  }
}
